@media screen and (max-width:500px) {
    body{
        
        section{
            //Nav
            nav{
                .container{
                    width: 100%;
                    .flexbox{
                        .flex{
                            &:nth-child(1){
                                width: 35% !important;
                                padding: 25px 0;

                                .logo_container{
                                    display: grid;
                                    place-items: center;
                                }
                            }

                            &:nth-child(2){
                                display: none !important;
                            }
                        }
                    }
                }
            }

            //Footer
            .footer{
                .container{
                    flex-direction: column;
                }
                
            }

        }
    }
}